.line {
    width: 100%;
    border-bottom: 1px solid #ddd;
    margin-bottom: 1em;
}

.line-form {
    width: 60%;
    height: 1px;
    border-bottom: 1.5px solid #000;
    margin-left: auto;
    margin-right: auto;
}
.h2-form{
    text-align: center;
    font-size: 1.4em;
}
.h2-titulo-secao{
    font-family:'Comfortaa';
    font-size: 1.7em;
    font-weight: 400;
}
label{
    font-size: 1em;
}

.form-control:focus {
    border-color: #565e64ab;
    box-shadow: none;
} 

.btn-custom {
	padding: 1px 15px 3px 2px;
	border-radius: 50px;
}

.btn-icon {
	padding: 8px;
	background: #ffffff;
    margin-left: 0;
}
.modal-title{
    font-weight: 500;
    font-size: 1.3em;
}
.modal-body{
    padding: 1.3em;
}
.erroForm{
    color: #842029;
    font-size: 0.9em;
    padding: 0;
    margin: 0;
}
.mensagemForm{
    font-size: 0.9em;
    margin-top: 10px;
    margin-bottom: 0;
    padding: 5px 5px 0px 5px;
    border-radius: .25rem;
}
.mensagemForm-Sucesso{
    color: #0f5132;
    background-color: #d1e7dd;
    border-color: #badbcc;
}
.mensagemForm-Erro{
    color: #842029;
    background-color: #f8d7da;
    border-color: #f5c2c7;
}
.mensagemForm-Alerta{
    color: #664d03;
    background-color: #fff3cd;
    border-color: #ffecb5;
}
