.secaoItens{
    border: 1px solid #ced4da;
    /*margin-top: 1em;*/
    border-radius: 8px;
    overflow-y: scroll;
    height: 8.5em;
    position: relative;
    padding-top: 5%;
    padding-bottom: 2%;
}
.secaoItensFecharOS{
    border: 1px solid #ced4da;
    /*margin-top: 1em;*/
    border-radius: 8px;
    position: relative;
    padding-top: 2%;
    padding-bottom: 2%;
}
#title {
    position: relative;
    /*top: -1.3em;*/

    display: inline;
    background-color: white;
  }
.btnMais{
    float: right;
    width: 30px;
    position: absolute;
    top: 0.5em;
    right: 0.5em;
}
.btExcluirItem{
    display: flex;
    align-items: end;
    justify-content: center;
    margin-bottom: 0.2em;
}